import { GET } from '../constants';
import BaseEndpoint from '../BaseEndpoint';
import { IBrandResult, IBrandsResult } from '../interfaces/Brand';
import { IQuery } from '../interfaces/Query';
import { parseParams } from '../helpers/parseParams';
import { Routes } from '../routes';

export default class Brand extends BaseEndpoint {
  
  public async list(params: IQuery = {}): Promise<IBrandsResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.listBrand(), parseParams(params) ) as IBrandsResult
  }
  
  public async get(id: string): Promise<IBrandResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.getBrand(id)) as IBrandResult
  }
}
