import { GET } from '../constants';
import BaseEndpoint from '../BaseEndpoint';
import { ICategoryResult, ICategoriesResult } from '../interfaces/Category';
import { IQuery } from '../interfaces/Query';
import { parseParams } from '../helpers/parseParams';
import { Routes } from '../routes';

export default class Category extends BaseEndpoint {
  
  public async list(params: IQuery = {}): Promise<ICategoriesResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.listCategory(), parseParams(params) ) as ICategoriesResult
  }
  
  public async get(id: string): Promise<ICategoryResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.getCategory(id)) as ICategoryResult
  }
}
