import { GET } from '../constants';
import BaseEndpoint from '../BaseEndpoint';
import { IOptionResult, IOptionsResult } from '../interfaces/Option';
import { IQuery } from '../interfaces/Query';
import { parseParams } from '../helpers/parseParams';
import { Routes } from '../routes';

export default class Option extends BaseEndpoint {
  
  public async list(params: IQuery = {}): Promise<IOptionsResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.listOption(), parseParams(params) ) as IOptionsResult
  }
  
  public async get(id: string): Promise<IOptionResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.getOption(id) ) as IOptionResult
  }
}
