import { GET, POST } from '../constants';
import BaseEndpoint from '../BaseEndpoint';
import { IContentResult } from '../interfaces/Content';
import { IQuery } from '../interfaces/Query';
import { Routes } from '../routes';
import { 
  IContentData
} from '../interfaces/requests/index';

export default class Content extends BaseEndpoint {
    
  public async get(params: IContentData): Promise<IContentResult> {
    return await this.sdkScope.httpService.slatwallResponse(POST, Routes.getContent(), params) as IContentResult;
  }
}
