import { GET } from '../constants';
import BaseEndpoint from '../BaseEndpoint';
import { IProductTypeResult, IProductTypesResult } from '../interfaces/ProductType';
import { IQuery } from '../interfaces/Query';
import { parseParams } from '../helpers/parseParams';
import { Routes } from '../routes';

export default class ProductType extends BaseEndpoint {
  
  public async list(params: IQuery = {}): Promise<IProductTypesResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.listProductType(), parseParams(params) ) as IProductTypesResult
  }
  
  public async get(id: string): Promise<IProductTypeResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.getProductType(id) ) as IProductTypeResult
  }
}
