import { GET } from '../constants';
import BaseEndpoint from '../BaseEndpoint';
import { ILocationResult, ILocationsResult } from '../interfaces/Location';
import { IQuery } from '../interfaces/Query';
import { parseParams } from '../helpers/parseParams';
import { Routes } from '../routes';

export default class Option extends BaseEndpoint {
  
  public async list(params: IQuery = {}): Promise<ILocationsResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.listLocation(), parseParams(params) ) as ILocationsResult
  }
  
  public async get(id: string): Promise<ILocationResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.getLocation(id) ) as ILocationResult
  }
  
  public async countries(): Promise<ILocationsResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.getCountries() ) as ILocationsResult
  }
  
  public async states(params: {countryCode: string}): Promise<ILocationsResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.getStates(), params ) as ILocationsResult
  }
  
  public async addressOptions(params: {}): Promise<ILocationsResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.getAddressOptions(), params ) as ILocationsResult
  }
  
}
