import { POST } from '../constants';
import BaseEndpoint from '../BaseEndpoint';
import { IQuery } from '../interfaces/Query';
import { AuthLoginAttr, IAuthResponseResult, AuthAPIKeyAttr } from '../interfaces/Authentication';
import { Routes } from '../routes';

export default class Auth extends BaseEndpoint {
  
  public async login(params: AuthLoginAttr): Promise<IAuthResponseResult> {
    let result = await this.sdkScope.httpService.slatwallResponse(POST, Routes.login(), params) as IAuthResponseResult;
    if ( result.isSuccess() ) {
      //Set bearerToken in local Storage
      this.sdkScope.bearerToken = result.success().token;
    } else {
      this.sdkScope.bearerToken = null;
    }
    return result;
  }
  
  public async generateToken(params: AuthAPIKeyAttr): Promise<IAuthResponseResult> {
    let result = await this.sdkScope.httpService.slatwallResponse(POST, Routes.login(), params) as IAuthResponseResult;
    if ( result.isSuccess() ) {
      //Set bearerToken in local Storage
      this.sdkScope.bearerToken = result.success().token;
    } else {
      this.sdkScope.bearerToken = null;
    }
    return result;
  }
  
  public async revokeToken(params: IQuery = {}): Promise<IAuthResponseResult> {
    this.sdkScope.bearerToken = null;
    return await this.sdkScope.httpService.slatwallResponse(POST, Routes.logout(), params) as IAuthResponseResult;
  }
  
}
