import { GET, POST } from '../constants';
import BaseEndpoint from '../BaseEndpoint';
import { IProductResult, IProductsResult } from '../interfaces/Product';
import { IProductOptionGroupResult } from '../interfaces/ProductOptionGroup';
import { IQuery, productOptionGroup } from '../interfaces/Query';
import { parseParams } from '../helpers/parseParams';
import { Routes } from '../routes';
import { IProductsGetGallery, 
  IProductsGetOptionsByOptionGroup, 
  IProductsGetRelatedProducts, 
  IProductsGetReviews, 
  IProductsGetStock 
} from '../interfaces/requests/index';

export default class Products extends BaseEndpoint {
  
  public async list(params: IQuery = {}): Promise<IProductsResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.listProduct(), parseParams(params) ) as IProductsResult
  }
  
  public async get(id: string): Promise<IProductResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.getProduct(id) ) as IProductResult
  }
  
  public async getGallery(params: IProductsGetGallery): Promise<IProductsResult> {
    return await this.sdkScope.httpService.slatwallResponse(POST, Routes.getProductGallery(), params ) as IProductsResult
  }
  
  public async getOptionsByOptionGroup(params: IProductsGetOptionsByOptionGroup): Promise<IProductOptionGroupResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.getProductOptionsByOptionGroup(), params ) as IProductOptionGroupResult
  }
  
  public async listSku(params: IQuery = {}): Promise<IProductsResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.listSku(), parseParams(params) ) as IProductsResult
  }
  
  public async getSku(id: string): Promise<IProductResult> {
    return await this.sdkScope.httpService.slatwallResponse(GET, Routes.getSku(id) ) as IProductResult
  }
  
  public async getRelatedProducts(params: IProductsGetRelatedProducts): Promise<IProductsResult> {
    return await this.sdkScope.httpService.slatwallResponse(POST, Routes.getRelatedProducts(), params ) as IProductsResult
  }
  
  public async getReviews(params: IProductsGetReviews): Promise<IProductsResult> {
    return await this.sdkScope.httpService.slatwallResponse(POST, Routes.getProductReviews(), params ) as IProductsResult
  }
  
  public async getStock(params: IProductsGetStock): Promise<IProductsResult> {
    return await this.sdkScope.httpService.slatwallResponse(POST, Routes.getSkuStock(), params ) as IProductsResult
  }
  
}
