import { POST } from '../constants';
import BaseEndpoint from '../BaseEndpoint';
import { IOrderResult } from '../interfaces/Order';
import { IQuery } from '../interfaces/Query';
import { parseParams } from '../helpers/parseParams';
import { Routes } from '../routes';
import { IOrderDuplicate, IOrderDelete, IOrderSetOrderOnCart } from '../interfaces/requests/index';
export default class Order extends BaseEndpoint {
  
  public async get(params: IQuery = {}): Promise<IOrderResult> {
    return await this.sdkScope.httpService.slatwallResponse(POST, Routes.getOrderDetails(), params ) as IOrderResult
  }
  
  public async duplicate(params: IOrderDuplicate): Promise<IOrderResult> {
    return await this.sdkScope.httpService.slatwallResponse(POST, Routes.duplicateOrder(), params ) as IOrderResult
  }
  
  public async delete(params: IOrderDelete): Promise<IOrderResult> {
    return await this.sdkScope.httpService.slatwallResponse(POST, Routes.deleteOrder(), params ) as IOrderResult
  }
  
  public async setOrderOnCart(params: IOrderSetOrderOnCart): Promise<IOrderResult> {
    return await this.sdkScope.httpService.slatwallResponse(POST, Routes.changeOrder(), params ) as IOrderResult
  }
}
